@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;600&display=swap");

* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}

html,
body {
  height: 100%;
}

body {
  font-size: 1.6rem;
  font-family: "Poppins", sans-serif;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.pinaka {
  background-color: #de182e;
}

.mataas {
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 75rem;
  margin: 0px auto;
  padding: 2rem;
}

.mataas > nav {
  display: flex;
  justify-content: space-between;
  gap: 7rem;
}

nav > * {
  transition: transform 0.15s ease-in-out 0s;
}

nav > *:hover {
  transform: scale(1.2);
}

.simbolo {
  display: flex;
  align-self: center;
  position: relative;
}

.loader {
  align-items: center;
  margin: 0 auto;
  display: flex;
}

main {
  background-image: linear-gradient(
    to top,
    #de182e,
    #f45d87,
    #f29acb,
    #edd0f3,
    #ffffff
  );

  display: flex;
  flex: 1;
}

.lagayan-ng-kard {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 3rem;
  gap: 2rem;
  max-width: 77rem;
  margin: 0px auto;
}

.kard {
  background-color: aliceblue;
  max-width: 340px;
  padding: 1rem;
  border-radius: 16px;
  border: 1px solid #e1e1e1;
  font-size: 1.4rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.kard img {
  width: 100%;
}

.kard p {
  font-size: 1rem;
}

.kard button {
  padding: 0.5rem;
  background-color: white;
  color: #de182e;
  font-size: 1rem;
  border: 1px solid #de182e;
  border-radius: 16px;
  transition: 0.2s ease-in-out;
}

.kard button:hover {
  background-color: #de182e;
  color: white;
}

.kard button:active {
  background-color: white;
  color: #de182e;
}

footer {
  color: white;
  text-align: center;
  font-size: 1rem;
  padding: 0.5rem;
  width: 100%;
}

a {
  text-decoration: none;
  color: white;
}

video {
  max-width: 100%;
}
